// import SeatCharts from './SeatCharts'
export default {
  // components: {SeatCharts},
  data () {
    return {
      value: [],
      isSelInsurance: false,
      price: 88
    }
  },
  methods: {
    goMap () {
      this.$router.push('/map')
    },
    selInsurance () {
      this.isSelInsurance = !this.isSelInsurance
      if (this.isSelInsurance) {
        this.price = this.price + 3
      } else {
        this.price = this.price - 3
      }
    },
    gotoBuy () {
      this.$router.push('/home/order_detail')
    }
  }
}
